.containe {
	font-family: Font-Thin;
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 4px;
  cursor: pointer;
  font-size: 15px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.containe input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
 
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  left: 0;
  margin-top: 2px;
  height: 14px;
  width: 14px;
  border:solid #60C3A1 1px;
  background-color: white;
  border-radius: 50%;
}
.room-list-heading{
  align-items: center;
}
/* On mouse-over, add a grey background color */
.containe:hover input ~ .checkmark {
  background-color: #60C3AD;
}

/* When the radio button is checked, add a blue background */
.containe input:checked ~ .checkmark {
  background-color: #60C3AD;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */

/* Style the indicator (dot/circle) */
.containe .checkmark:after {
 	top: 9px;
	left: 9px;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: white;
}

